import React from "react";
import { Breadcrumb, Button, ButtonGroup, Row, Col, InputGroup, Form, Dropdown, Table, Card, DropdownButton } from "@themesberg/react-bootstrap";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faPlus, faCog, faCheck, faSearch, faSlidersH } from '@fortawesome/free-solid-svg-icons';

import Popup from 'reactjs-popup';    
import teams from '../data/teams';
// import RankCard from '../components/';

class RankCard extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            clicked: [false]
        };
        for (var i = 1; i <= 8; i++) {
            this.state.clicked.push(false);
        }
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(e, ID) {
        var clickArr = [false];
        for (var i = 1; i <= 8; i++) {
            clickArr.push(i == ID ? true : false);
        }
        this.setState({
            clicked: clickArr
        });

        console.log(this.state.clicked);
    }

    render() {
        return (
            <tbody>
                { teams.map((team) => (
                    <>
                        <tr onClick={ (e) => this.handleClick(e, team.ID) }>
                            <th className="border-bottom" style={{ "fontSize": 22, "fontWeight": "bold", "align": "center" }}>#{ team.rank }</th>
                            <th className="border-bottom" style={{ "fontSize": 18 }}>{ team.ID }</th>
                            <th className="border-bottom" style={{ "fontSize": 18, "fontStyle": "italic" }}>{ team.score }</th>
                        </tr>
                        {
                            this.state.clicked[team.ID] ? (
                                <tr>
                                    <th colSpan={ 3 } style={{ "background": "#9ac4fa" }}>
                                        <Table borderless className="user-table align-items-center">
                                            <thead>
                                                <tr>
                                                    <th className="border-bottom" style={{ "fontSize": 18}}>RFID</th>
                                                    <th className="border-bottom" style={{ "fontSize": 18}}>Score</th>
                                                    <th className="border-bottom" style={{ "fontSize": 18}}>Timestamp</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <th className="border-bottom" style={{ "fontSize": 18, "fontWeight": "bold" }}>12345678</th>
                                                    <th className="border-bottom" style={{ "fontSize": 14, "fontStyle": "italic" }}>12</th>
                                                    <th className="border-bottom" style={{ "fontSize": 14 }}>2022-07-01 01:29</th>
                                                </tr>
                                            </tbody>
                                        </Table>
                                    </th>
                                </tr>
                            ) : (
                                <></>
                            )
                        }
                    </>
                )) }
            </tbody>
        );
    }
}

export default () => {

    return (
        <div className="d-lg-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
            <div className="mb-4 mb-lg-0" style={{ width: '100%' }}>
                <div className="table-settings mb-4">
                    <Row className="justify-content-between align-items-center">
                        <Col xs={9} lg={4} className="d-flex">
                            <InputGroup className="me-2 me-lg-3">
                                <InputGroup.Text>
                                    <FontAwesomeIcon icon={faSearch} />
                                </InputGroup.Text>
                                <Form.Control type="text" placeholder="Search" />
                            </InputGroup>
                            <Form.Select className="w-25">
                                <option defaultChecked>All</option>
                                <option value="1">Active</option>
                                <option value="2">Inactive</option>
                                <option value="3">Pending</option>
                                <option value="3">Canceled</option>
                            </Form.Select>
                        </Col>
                        <Col xs={3} lg={8} className="text-end">
                            <Dropdown as={ButtonGroup} className="me-2">
                                <Dropdown.Toggle split as={Button} variant="link" className="text-dark m-0 p-0">
                                    <span className="icon icon-sm icon-gray">
                                        <FontAwesomeIcon icon={faSlidersH} />
                                    </span>
                                </Dropdown.Toggle>
                                <Dropdown.Menu className="dropdown-menu-right">
                                    <Dropdown.Item className="fw-bold text-dark">Show</Dropdown.Item>
                                    <Dropdown.Item className="d-flex fw-bold">
                                        10 <span className="icon icon-small ms-auto"><FontAwesomeIcon icon={faCheck} /></span>
                                    </Dropdown.Item>
                                    <Dropdown.Item className="fw-bold">20</Dropdown.Item>
                                    <Dropdown.Item className="fw-bold">30</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                            <Dropdown as={ButtonGroup}>
                                <Dropdown.Toggle split as={Button} variant="link" className="text-dark m-0 p-0">
                                    <span className="icon icon-sm icon-gray">
                                        <FontAwesomeIcon icon={faCog} />
                                    </span>
                                </Dropdown.Toggle>
                                <Dropdown.Menu className="dropdown-menu-right">
                                    <Dropdown.Item className="fw-bold text-dark">Show</Dropdown.Item>
                                    <Dropdown.Item className="d-flex fw-bold">
                                        10 <span className="icon icon-small ms-auto"><FontAwesomeIcon icon={faCheck} /></span>
                                    </Dropdown.Item>
                                    <Dropdown.Item className="fw-bold">20</Dropdown.Item>
                                    <Dropdown.Item className="fw-bold">30</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </Col>
                    </Row>
                </div>

                <Card border="light" className="table-wrapper table-responsive shadow-sm">
                    <Card.Body>
                        <Table borderless className="user-table align-items-center">
                            <thead>
                                <tr>
                                    <th className="border-bottom" style={{ "fontSize": 24}}>Rank</th>
                                    <th className="border-bottom" style={{ "fontSize": 24}}>Team ID</th>
                                    <th className="border-bottom" style={{ "fontSize": 24}}>Score</th>
                                </tr>
                            </thead>
                            <RankCard></RankCard>
                        </Table>
                    </Card.Body>
                </Card>
            </div>
        </div>
    );
};
